div.cards,
div.episode-scroller {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-flow: row wrap;
	        flex-flow: row wrap;
	-webkit-align-content: space-between;
	        align-content: space-between;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	overflow-x: auto;
	-webkit-overflow-scrolling: touch
}

div.cards div.card-content,
div.episode-scroller div.card-content {
	padding: .5rem
}

div.cards div.card,
div.episode-scroller div.card {
	width: 19%;

	margin-bottom: 1rem;
	-webkit-flex: 0 0 auto;
	        flex: 0 0 auto;
	box-shadow: none;
	background-color: transparent
}

div.cards div.card div.episode-meta,
div.episode-scroller div.card div.episode-meta {
	line-height: 1rem;
	color: rgb(0, 119, 230);
}

div.cards div.card div.episode-meta span.episode-title,
div.episode-scroller div.card div.episode-meta span.episode-title {
	display: block;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
	font-weight: 700;
	font-size: 1rem;
	color: #fff;
}


.has-text-danger{
	color: red;
	font-size: 0.9rem;

}
div.cards div.card div.episode-meta span.episode-num,
div.episode-scroller div.card div.episode-meta span.episode-num {
	display: block;
	font-weight: 700;
	font-size: .8rem;
	color: #97b3c5
}

div.card a:hover p.title {
	text-decoration: none;
	color: #000
}

div.card a:hover small {
	text-decoration: none
}

ul.search-results li {
	margin: 2rem
}


div.scroller {
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
	display: -webkit-flex;
	display: flex
}

div.scroller div.card {
	border-radius: .5em
}


.latest-anime-div h1{
	color: white;
	width: 100vw;
	text-align: left;
	font-family: 'Mukta', sans-serif;
	font-size: 2rem;
	font-weight: bold;
}
.latest-anime-cards{
	display: -webkit-flex;
	display: flex;
	background-color: #111111;
	-webkit-flex-direction: row;
	        flex-direction: row;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
	margin: 0;

}

.latest-anime-div .latest-cards{
	margin: 1vh 1vw;
	
}
.latest-anime-cards figure{
	width: 28vh;
	border: 4px solid grey;


}

.latest-anime-cards figure img{
	width: 28vh;
	height: 250px;
}
.latest-anime-cards figure figcaption{
	color: white;
	font-family: 'Odibee Sans', cursive;
}

/* Mobile */
@media only screen and (max-width:500px) {
	div.episode-scroller {
		overflow-x: scroll;
		-webkit-overflow-scrolling: touch
	}
	div.episode-scroller div.card {
		width: 70%
	}
	div.cards {
		-webkit-align-content: center;
		        align-content: center;
		-webkit-justify-content: center;
		        justify-content: center
	}
	div.cards div.card {
		width: 100%
	}
	.latest-anime-cards figure{
		width: 50vw;
	}
	
	.latest-anime-cards figure img{
		width: 50vw;

	}
	

}


/* hd mobile */
@media only screen and (max-width:650px) {
	div.scroller {
		display: -webkit-flex;
		display: flex;
		-webkit-flex-wrap: nowrap;
		        flex-wrap: nowrap;
		overflow-x: auto
	}
	div.scroller div.anime-box,
	div.scroller div.card {
		-webkit-flex: 0 0 auto;
		        flex: 0 0 auto;
		width: 80%;
		margin-right: 2rem;
		border-radius: .5em;
		margin-bottom: 2rem
	}
	.latest-anime-cards .latest-cards figure{
		width: 100vw;
		height: 320px;
	}

	.latest-anime-cards .latest-cards  figure img{
		width: 100vw;
	}

}


/* @tablet */
@media only screen and (max-width:769px) {
	div.card-title-limit p.title {
		width: auto;
		white-space: normal;
		overflow: visible;
		text-overflow: clip
	}
	.latest-anime-cards .latest-cards figure{
		width: 20vw;
		height: 320px;
	}

	.latest-anime-cards .latest-cards figure img{
		width: 20vw;
	}
	
}

/* @small screen laptop */
@media only screen and (max-width:1087px) {
	div.episode-scroller {
		-webkit-flex-wrap: nowrap;
		        flex-wrap: nowrap;
		overflow-x: scroll;
		-webkit-overflow-scrolling: touch
	}
	div.episode-scroller div.card {
		margin: .5rem;
		width: 40%
	}
	.hero.is-black div.hero-body {
		padding: 1rem!important
	}
	.latest-anime-cards{
		-webkit-justify-content: center;
		        justify-content: center;
		-webkit-align-items: center;
		        align-items: center;
	}
	
	.latest-anime-cards{
		height: 400px;
		letter-spacing: 1px;
		-webkit-flex-direction: column;
		        flex-direction: column;
		-webkit-flex-wrap: no-wrap;
		        flex-wrap: no-wrap;
		overflow-y: scroll;
	}
	.latest-anime-cards .latest-cards figure{
		width: 20vw;
		height: 320px;
	}

	.latest-anime-cards .latest-cards figure img{
		width: 20vw;
	}

}


/* @normal screen laptop */
@media only screen and (max-width:1279px) {
	div.scroller {
		display: -webkit-flex;
		display: flex;
		-webkit-flex-wrap: nowrap;
		        flex-wrap: nowrap;
		overflow-x: scroll;
		-webkit-overflow-scrolling: touch
	}
	div.scroller div.anime-box,
	div.scroller div.card {
		-webkit-flex: 0 0 auto;
		        flex: 0 0 auto;
		width: 30%;
		margin-bottom: 2rem
	}
	.latest-anime-cards{
		padding: 0;
	}
	
	.latest-anime-cards figure img{
		width: 17.5vw;
		height: 300px;
	}
	.latest-anime-cards figure{
		width: 17.5vw;
		height: 350px;
	
	}
}


/* @large screen laptop */
@media only screen and (max-width:1400px) {
	
	.latest-anime-cards figure img{
		width: 14.5vw;
		height: 250px;
	}
	.latest-anime-cards figure{
		width: 14.2vw;
		border: 4px solid grey;
	
	}
	

}



.search-body{
    color: black;
    margin-top: 20vh;
}
